import React from "react";
import { Link, Box, Flex, Text, Button, Stack, Heading } from "@chakra-ui/react";
import AniLink from "gatsby-plugin-transition-link/AniLink"
import { AnchorLink } from "gatsby-plugin-anchor-links";
import { AnimatePresence, motion } from "framer-motion";
//import Logo from "./Logo";
const menuVars = {
  initial: {
    scaleY: 0,
  },
  animate: {
    scaleY: 1,
    transition: {
      duration: 0.5,
      ease: [0.12, 0, 0.39, 0],
    },
  },
  exit: {
    scaleY: 0,
    transition: {
      delay: 0.5,
      duration: 0.5,
      ease: [0.22, 1, 0.36, 1],
    },
  },
};

const containerVars = {
  initial: {
    transition: {
      staggerChildren: 0.09,
      staggerDirection: -1,
    },
  },
  open: {
    transition: {
      delayChildren: 0.3,
      staggerChildren: 0.09,
      staggerDirection: 1,
    },
  },
};


const NavBar = (props) => {
  const [isOpen, setIsOpen] = React.useState(false);

  const toggle = () => setIsOpen(!isOpen);
  const navLinks = [
    { title: "Home Page", href: "/" },
    { title: "About", href: "/#about" },
    { title: "Works", href: "/#works" },
    { title: "Contact", href: "/#contact" },
  ];

  return (
    <NavBarContainer {...props}>
        <Flex  w='100%' h='50px' borderRadius='30px' m='10px' justifyContent={'space-between'}>
        <AniLink paintDrip to="/" color={"black"} >
          <Text fontWeight={'600'}>Keyaria Walker</Text>
        </AniLink>
      <MenuToggle toggle={toggle} isOpen={isOpen} zIndex='200'/>
      <MenuLinks isOpen={isOpen} />
      <AnimatePresence>
        {isOpen && (
          <Box
             as={motion.div}
            variants={menuVars}
            initial="initial"
            animate="animate"
            exit="exit"
            pos='fixed'
            left='0'
            top='0'
            w='100%'
            h='100vh'
            bg='black'
            color='white'
            p='10'
            zIndex={'100'}
          >
            <Flex h='100%' flexDir='column' className="flex h-full flex-col">
              <Flex justifyContent='space-between' alignItems={'center'} >
              <AniLink paintDrip to="/" color={"black"} >
 <Heading  className="text-lg text-black">Keyaria Walker</Heading>
 </AniLink>
                <Box display={{ base: "block", md: "none" }} onClick={toggle}>
      <CloseIcon /> 
    </Box>
              </Flex>
              <Flex as={motion.div}
                variants={containerVars}
                initial="initial"
                animate="open"
                exit="initial"
                h='full'
                justifyContent='center'
                alignItems={'center'}
                gap={'4px'}
                flexDirection={'column'}
                className="flex flex-col h-full justify-center font-lora items-center gap-4 "
              >
                {navLinks.map((link, index) => {
                  return (
                    <Box overflow={'hidden'} className="overflow-hidden">
                      <MobileNavLink
                        key={index}
                        title={link.title}
                        href={link.href}
                      />
                    </Box>
                  );
                })}
              </Flex>
            </Flex>
          </Box>
        )}
      </AnimatePresence>
      </Flex>
    </NavBarContainer>
  );
};

const CloseIcon = () => (
  <svg width="24" viewBox="0 0 18 18" xmlns="http://www.w3.org/2000/svg">
    <title>Close</title>
    <path
      fill="white"
      d="M9.00023 7.58599L13.9502 2.63599L15.3642 4.04999L10.4142 8.99999L15.3642 13.95L13.9502 15.364L9.00023 10.414L4.05023 15.364L2.63623 13.95L7.58623 8.99999L2.63623 4.04999L4.05023 2.63599L9.00023 7.58599Z"
    />
  </svg>
);

const MenuIcon = () => (
  <svg
    width="32px"
    viewBox="0 0 20 20"
    xmlns="http://www.w3.org/2000/svg"
    fill="black"
  >
    <title>Menu</title>
    <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
  </svg>
);

const MenuToggle = ({ toggle, isOpen }) => {
  return (
    <Box display={{ base: "block", md: "none" }} onClick={toggle}>
      {isOpen ? <CloseIcon /> : <MenuIcon />}
    </Box>
  );
};

const MenuItem = ({ children, isLast, to = "/", ...rest }) => {
 return(
    <AnchorLink
    to={to}> 
      <Text display="block" {...rest} >
        {children}
      </Text>
      </AnchorLink>
  )

//   return (
// {path != '/' }
//     <AnchorLink
//     to={to}> 
//       <Text display="block" {...rest} >
//         {children}
//       </Text>
//       </AnchorLink>
//   );
};

const MenuLinks = ({ isOpen }) => {
    
  return (
    <Box
      display={{ base: "none", md: "block" }}
      flexBasis={{ base: "100%", md: "auto" }}
    >
      <Stack
        spacing='4rem'
        textTransform='uppercase'
        align="center"
        justify={["center", "space-between", "flex-end", "flex-end"]}
        direction={["column", "row", "row", "row"]}
        pt={['4px', '4px', 0, 0]}
      >
               <AniLink paintDrip to="/" color="black">
Home</AniLink>
<MenuItem to="/#about">About </MenuItem>
        <MenuItem to="/#works">Works</MenuItem>
        <MenuItem to="/#contact">Contact </MenuItem>
        {/* <MenuItem to="/signup" isLast>
          <Button
            size="sm"
            rounded="md"
            color={["red.500", "red.500", "black", "black"]}
            bg={["white", "white", "white", "White"]}
            _hover={{
              bg: ["red.100", "red.100", "red.600", "red.600"]
            }}
          >
            EN
          </Button>
        </MenuItem> */}
      </Stack>
    </Box>
  );
};
const mobileLinkVars = {
  initial: {
    y: "30vh",
    transition: {
      duration: 0.5,
      ease: [0.37, 0, 0.63, 1],
    },
  },
  open: {
    y: 0,
    transition: {
      ease: [0, 0.55, 0.45, 1],
      duration: 0.7,
    },
  },
};
const MobileNavLink = ({ title, href }) => {
  return (
    <Text as ={motion.div}
    fontSize='5xl'
      variants={mobileLinkVars}
      className="text-5xl uppercase text-black"
    >
      <Link href={href}>{title}</Link>
    </Text>
  );
};
const NavBarContainer = ({ children, ...props }) => {
  return (
    <Flex
      as="nav"
      align="center"
      justify="space-between"
      wrap="wrap"
      w="100%"
      mb='12px'
      p='16px'
      bg={["transparent", "transparent", "transparent", "transparent"]}
      color={["black", "black", "black", "black"]}
      {...props}
    >
      {children}
    </Flex>
  );
};

export default NavBar;
